.callback {
  &__wrapper {
    @include screen(laptop) {
      display: grid;
      grid-template-columns: 1fr 55%;
    }
  }
  &__image {
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  &__content {
    background-color: $red;
    padding: 34px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include screen(laptop) {
      align-items: flex-start;
      padding: 134px 106px 148px;
    }
    h2 {
      font-weight: 600;
      font-size: 24px;
      line-height: 100%;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      text-align: center;
      @include screen(tablet) {
        font-size: 42px;
        text-align: left;
      }
    }
    .button {
      margin-top: 25px;
      display: inline-block;
      @include screen(tablet) {
        margin-top: 35px;
      }
    }
  }
}