.marquee {
  height: 50px;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px 0 0;
  z-index: 2;
  overflow: hidden;
  user-select: none;
  background-color: $purple-dark;
  @include screen(tablet) {
    height: 128px;
  }
  &-black {
    background-color: $black;
  }
  &__item {
    flex: 0 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    line-height: 18px;
    font-family: $DXRigrafEx;
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 20px;
    color: $white;
    @include screen(tablet) {
      font-size: 42px;
      line-height: 40px;
    }
  }
  &__items {
    animation: scroll 40s linear infinite;
    flex-shrink: 0;
    display: flex;
    counter-reset: item;
    justify-content: space-around;
    min-width: 100%;
  }
}
.reverce {
  animation-direction: reverse;
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - 20px));
  }
}