.rosters {
  background-color: $purple-dark;
  &__item {
    padding: 40px 0;
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    @include screen(laptop) {
      flex-direction: row;
      padding: 65px 0;
    }
    &-reversed {
      @include screen(laptop) {
        flex-direction: row-reverse;
      }
    }
    &:nth-child(1) {
      .rosters__content img {
        top: -85px;
        left: 10px;
      }
    }
    &:nth-child(2) {
      .rosters__content img {
        right: 20px;
        top: -15px;
      }
    }
  }
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-width: 80%;
      margin: 0 auto;
      @include screen(tablet) {
        max-width: 100%;
      }
    }
    @include screen(laptop) {
      width: 45%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 25px;
    position: relative;
    @include screen(laptop) {
      row-gap: 48px;
      width: 55%;
    }
    img {
      position: absolute;
      @include screen(tablet) {
        display: none;
      }
    }
  }
  &__title {
    font-size: 32px;
    line-height: 30px;
    font-weight: 300;
    text-transform: uppercase;
    @include screen(tablet) {
      font-size: 82px;
      line-height: 1;
    }
    span {
      font-family: $DXRigrafEx;
      font-weight: 700;
    }
  }
  &__text {
    font-family: $Inter;
    font-size: 14px;
    line-height: 20px;
    max-width: 87%;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 36px;
      max-width: auto;
    }
    br {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    span {
      font-weight: 700;
    }
  }
}