.special-prices {
  padding: 50px 0 160px;
  position: relative;
  @include screen(tablet) {
    padding: 120px 0 187px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
  &__wrapper {
    row-gap: 20px;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    @include screen(tablet) {
      row-gap: 50px;
    }
    h2 {
      font-size: 32px;
      position: relative;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      line-height: 36px;
      i {
        font-style: normal;
        position: relative;
        &:before {
          content: "";
          position: absolute;
          bottom: -12px;
          z-index: -1;
          left: 0;
          width: 100%;
          height: 40px;
          background-image: url(data:image/svg+xml;base64,);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          @include screen(tablet) {
            bottom: 0;
          }
        }
      }
      @include screen(tablet) {
        font-size: 82px;
        line-height: 1;
      }
    }
  }
  &__content {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    @include screen(tablet) {
      max-width: 51%;
      margin-left: auto;
    }
    p {
      font-size: 14px;
      margin-top: 20px;
      font-family: $Inter;
      &:first-child {
        margin-top: 0;
      }
      @include screen(tablet) {
        margin-top: 20px;
      }
      br {
        display: none;
        @include screen(tablet) {
          display: none;
        }
      }
      @include screen(tablet) {
        margin-top: 24px;
        font-size: 24px;
        line-height: 120%;
      }
    }
    .button {
      margin-top: 24px;
    }
  }
}