.delivery {
  padding: 50px 0 83px;
  position: relative;
  @include screen(tablet) {
    padding: 187px 0 201px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
  &__wrapper {
    row-gap: 24px;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    @include screen(tablet) {
      row-gap: 134px;
    }
    h2 {
      position: relative;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      z-index: 2;
      position: relative;
      font-size: 32px;
      line-height: 36px;
      @include screen(tablet) {
        font-size: 42px;
        line-height: 48px;
      }
      br {
        &.br-pc {
          display: none;
          @include screen(mobile) {
            display: block;
          }
        }
        @include screen(mobile) {
          display: none;
        }
      }
      .line {
        &:before {
          content: "";
          position: absolute;
          bottom: -12px;
          z-index: -1;
          display: none;
          left: 0;
          width: 100%;
          height: 40px;
          background-image: url(data:image/svg+xml;base64,);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          @include screen(mobile) {
            bottom: 0;
            display: block;
          }
        }
        &-mob {
          &:before {
            content: "";
            position: absolute;
            bottom: -12px;
            z-index: -1;
            left: 0;
            width: 100%;
            height: 40px;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100%;
            @include screen(mobile) {
              bottom: 0;
              display: none;
            }
          }
        }
      }
      span {
        position: relative;
        z-index: 2;
      }
      &:after {
        content: "";
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        height: 100%;
        background-color: $black;
        filter: blur(100px);
      }
    }
  }
  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      background-color: $black;
      filter: blur(100px);
    }
    @include screen(tablet) {
      max-width: 51%;
      margin-left: auto;
    }
    p {
      font-size: 14px;
      z-index: 2;
      position: relative;
      font-family: $Inter;
      br {
        display: none;
        @include screen(tablet) {
          display: none;
        }
      }
      @include screen(tablet) {
        margin-top: 24px;
        font-size: 24px;
        line-height: 120%;
      }
    }
  }
}