.formula {
  padding: 80px 0;
  position: relative;
  @include screen(tablet) {
    padding: 187px 0 201px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
  &__wrapper {
    row-gap: 24px;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    @include screen(tablet) {
      row-gap: 30px;
    }
    h2 {
      position: relative;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      img {
        z-index: 2;
        position: relative;
      }
      span {
        z-index: 2;
        position: relative;
        font-size: 32px;
        line-height: 36px;
        @include screen(tablet) {
          font-size: 42px;
          line-height: 48px;
        }
      }
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        height: 100%;
        background-color: $black;
        filter: blur(100px);
      }
    }
  }
  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      height: 100%;
      background-color: $black;
      filter: blur(100px);
    }
    @include screen(tablet) {
      max-width: 51%;
      align-items: flex-start;
      margin-left: auto;
    }
    p {
      font-size: 14px;
      z-index: 2;
      position: relative;
      margin-top: 20px;
      font-family: $Inter;
      text-align: center;
      &:first-child {
        margin-top: 0;
      }
      br {
        display: none;
        @include screen(tablet) {
          display: none;
        }
      }
      @include screen(tablet) {
        margin-top: 24px;
        font-size: 24px;
        text-align: left;
        line-height: 120%;
      }
    }
    .button {
      z-index: 2;
      position: relative;
      margin-top: 24px;
    }
  }
}