.process {
  background-color: $purple;
  padding: 50px 0;
  @include screen(tablet) {
    padding: 117px 0;
  }
  &__wrapper {
    h2 {
      font-size: 32px;
      line-height: 36px;
      letter-spacing: 0.2px;
      text-transform: uppercase;
      font-family: $DXRigrafEx;
      @include screen(tablet) {
        font-size: 78px;
        line-height: 100px;
      }
      @include screen(desktop) {
        font-size: 82px;
      }
      span {
        color: $red;
      }
    }
    ol {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      row-gap: 20px;
      @include screen(tablet) {
        margin-top: 48px;
        row-gap: 26px;
      }
      li {
        display: flex;
        align-items: center;
        column-gap: 6px;
        @include screen(tablet) {
          column-gap: 15px;
        }
        span {
          text-transform: uppercase;
          font-family: $Inter;
          font-weight: 700;
          font-size: 20px;
          line-height: 120%;
          letter-spacing: 0.2px;
          @include screen(tablet) {
            font-size: 42px;
          }
        }
        svg {
          width: 36px;
          height: 36px;
          @include screen(tablet) {
            width: auto;
            height: auto;
          }
        }
      }
    }
  }
}